  }


  .color-gray100 {
    background-color: var(--nmds-gray100);
  }




.primary-hue {
  display: flex;
  padding:3rem 1rem;
  color:#ffffff;
  font-family: 'Work Sans';
}

.primary-hue > div {
  width:33%;
}

.secondary-hue {
  display: flex;
  padding:1.5rem 1rem;
  color:#ffffff;
  font-family: 'Work Sans';
}

.secondary-hue > div {
  width:33%;
}

.neutral-hue {
  display: flex;
  padding:1.5rem 1rem;
  color:#ffffff;
  font-family: 'Work Sans';
}

.neutral-hue > div {
  width:33%;
}

.accent-hue {
  display: flex;
  padding:.5rem 1rem;
  color:#ffffff;
  font-family: 'Work Sans';
}

.accent-hue > div {
  width:33%;
}

.text-dark {
  color:var(--text-color);
}





img {
  width:100%;
